import {isPlatformBrowser} from '@angular/common';
import {Inject, Injectable, PLATFORM_ID} from '@angular/core';

export enum PaymentDataType {
  BTOK = 'BTOK',
  SUBSCRIPTION = 'SUBSCRIPTION'
}

interface PaymentDataDto {
  total: number;
  type: PaymentDataType;
  voucher: string;
}

interface PaymentSubscriptionDto {
  planOption: PlanOptionId[];
}

interface PlanOptionId {
  id: string;
  referralCode?: string
}

@Injectable({
  providedIn: 'root'
})
export class PaymentData {
  isReady = false;
  paymentData!: PaymentDataDto;
  paymentSubscriptionData!: PaymentSubscriptionDto;

  constructor(@Inject(PLATFORM_ID) private platformId: object) {
    if (isPlatformBrowser(this.platformId)) {
      this.isReady = true;
    }
  }

  /** PAYMENT */
  set payment(data: PaymentDataDto) {
    this.paymentData = data;
    if (this.isReady) {
      localStorage.setItem('paymentData', JSON.stringify(data));
    }
  }

  get payment(): PaymentDataDto {
    if (this.isReady) {
      const local = localStorage.getItem('paymentData');
      if (local) {
        const paymentData: PaymentDataDto = JSON.parse(local) as PaymentDataDto;
        return paymentData;
      } else {
        return {} as PaymentDataDto;
      }
    } else {
      return {} as PaymentDataDto;
    }
  }

  /** PAYMENT SUBSCRIPTION */
  set paymentSubscription(data: PaymentSubscriptionDto) {
    this.paymentSubscriptionData = data;
    if (this.isReady) {
      localStorage.setItem('paymentSubscriptionData', JSON.stringify(data));
    }
  }

  get paymentSubscription(): PaymentSubscriptionDto {
    if (this.isReady) {
      const local = localStorage.getItem('paymentSubscriptionData');
      if (local) {
        const paymentSubscriptionData: PaymentSubscriptionDto = JSON.parse(
          local
        ) as PaymentSubscriptionDto;
        return paymentSubscriptionData;
      } else {
        return {} as PaymentSubscriptionDto;
      }
    } else {
      return {} as PaymentSubscriptionDto;
    }
  }
}
