import {Component} from '@angular/core';
import {Modal} from "../../../shared/service/modal/modal.model";
import {InputModalDto} from "../../../shared/service/modal/modal.service";
import {
  Category, DetailWarehouseInput,
  ResponseDetailWarehouseDto,
   WarehouseService
} from "@youtap-web-lib";
import * as dayjs from "dayjs";
import "dayjs/locale/id";
import {WindowActionService} from "../../../shared/service/window-action.service";
dayjs.locale("id");

@Component({
  selector: 'ajukan-keluhan-modal',
  templateUrl: './ajukan-keluhan-modal.component.html'
})
export class AjukanKeluhanModalComponent extends Modal {

  detailWarehouse!: ResponseDetailWarehouseDto
  today = Number(dayjs().locale("id").format("d"));

  constructor(
    private warehouseService: WarehouseService,
    private windowActionService: WindowActionService
  ) {
    super();
  }

  onInjectInputs(inputs: InputModalDto): void {
    this.getDetailWarehouse(inputs.data.detail)
  }


  getDetailWarehouse(supplierSlug: string, slug?: string) {
    const data: DetailWarehouseInput = {
      detailWarehouseInput: {
        ...(supplierSlug && {supplierSlug: supplierSlug}),
        ...(slug && {slug: slug}),
      }
    };

    this.warehouseService.detailWarehouse(data).subscribe({
      next: ({data}) => {
        this.detailWarehouse = data
      }, error: (err) => {
        return err;
      }
    });
  }

  select(value: Category): void {
    this.close(value);
  }

  cancel(): void {
    this.dismiss({data: '1234'});
  }

  openEmail(mail: string) {
    this.windowActionService.href('mailto:' + mail);
  }
}
