import { Component, ContentChild, Input } from '@angular/core';

@Component({
  selector: 'button-footer',
  template: `
    <div
      [ngStyle]="{ display: isDisplay && 'none' }"
      class="fixed !w-[100%] bg-white px-[20px] pt-[20px] pb-[30px] rounded-t-3xl bottom-0 shadow-top-shadow max-w-[500px]"
      [ngClass]="margin">
      <ng-container *ngTemplateOutlet="footerButtonTemplate"></ng-container>
    </div>
  `
})
export class ButtonFooterComponent {
  // eslint-disable-next-line  @typescript-eslint/no-explicit-any
  @ContentChild('footerButtonTemplate') footerButtonTemplate: any;
  @Input() isDisplay = false;
  @Input() margin = 'm-0';
}
