import { Injectable } from '@angular/core';
import {ColorTypeDto, SCMTransactionSupplierStatusTypes} from "@youtap-web-lib";

@Injectable({
  providedIn: 'root'
})
export class TransactionHelperService {
  calculateTransaction(
    latest: number,
    oldest: number
  ): { colour: string; price: string } {
    let mark = '';
    let price = 0;
    let colour: ColorTypeDto = 'green';
    if (oldest === 0) {
      price = 100;
    } else {
      price = Math.ceil(((latest - oldest) / oldest) * 100);
    }

    if (price > 0) {
      colour = 'green';
      mark = '+';
    } else {
      colour = 'red';
    }

    return { price: `${mark}${price.toString()}%`, colour };
  }

  convertStatus(status: string): { label: string; color: ColorTypeDto } {
    switch (status) {
      case SCMTransactionSupplierStatusTypes.AWAITING_PAYMENT:
        return { label: 'Menunggu Pembayaran', color: 'orange' };
      case SCMTransactionSupplierStatusTypes.PENDING:
        return { label: 'Menunggu Konfirmasi', color: 'blue' };
      case SCMTransactionSupplierStatusTypes.PROCESSED:
        return { label: 'Pesanan Diproses', color: 'blue' };
      case SCMTransactionSupplierStatusTypes.ON_DELIVERY:
        return { label: 'Sedang Dikirim', color: 'orange' };
      case SCMTransactionSupplierStatusTypes.PACKAGE_RECEIVED:
        return { label: 'Pesanan Diterima', color: 'blue' };
      case SCMTransactionSupplierStatusTypes.SUCCESS:
        return { label: 'Selesai', color: 'green' };
      case SCMTransactionSupplierStatusTypes.FAILED:
        return { label: 'Pembayaran Gagal', color: 'red' };
      case SCMTransactionSupplierStatusTypes.CANCELED:
        return { label: 'Dibatalkan', color: 'red' };
      default:
        return { label: status, color: 'yellow' };

    }
  }
}
