/* eslint-disable @typescript-eslint/no-explicit-any */
import { Component, EventEmitter, Input, Output } from '@angular/core';
import { Router } from '@angular/router';
import {
  CartErrorCodeEnum,
  CartService,
  CreateCartInput,
  CreateCartWithMultipleProductInput,
  DetailTransactionSupplierInput,
  PaymentType,
  ResponseCheckCartDto,
  SCMTransactionService,
  Suppliers,
  TransactionDetail,
  TransactionSupplier,
  Warehouse
} from '@youtap-web-lib';
import { ColorTypeDto } from '@youtap-web-lib';
import { PaymentData, PaymentDataType } from '../../../data/payment.data';
import { TransactionHelperService } from '../../../service/transaction-helper.service';
import { CountCartService } from '../../../service/count-cart.service';
import { GaService } from '../../../service/ga.service';

export interface HistoryTransactionItemCardDto {
  id: string;
  supplierImage: string;
  supplierName: string;
  date: string;
  transactionStatus: {
    label: string;
    color: ColorTypeDto;
  };
  productVariantImage: string;
  productName: string;
  productVariantName: string;
  productQty: number;
  productOther?: number;
  total: number;
  supplierThirdPartyType?: string;
  supplier: Suppliers;
  warehouse: Warehouse;
  status: string;
  type: string;
  transactionDetails: TransactionDetail[];
}

export interface ProductVariantWarehouse {
  id: string;
}

export type actionDto = 'order' | 'complain' | 'payment' | 'detail';

@Component({
  selector: 'lib-history-transaction-item-card',
  templateUrl: './history-transaction-item-card.component.html'
})
export class HistoryTransactionItemCardComponent {
  @Input() historyTransaction!: HistoryTransactionItemCardDto;
  @Output() goTo = new EventEmitter<string>();
  @Output() action = new EventEmitter<actionDto>();
  @Output() errorAddCart = new EventEmitter<boolean>();
  onAction = true;
  isCheckLoading = false;
  authData: any;
  detailProduct: any;
  productVariantSelected: any;
  addLoading = false;
  cartSuccess = false;
  isLoadingCheckCart = false;
  dataCheckCart: ResponseCheckCartDto[] = [];

  constructor(
    private router: Router,
    private paymentData: PaymentData,
    private scmTransactionService: SCMTransactionService,
    private transactionHelperService: TransactionHelperService,
    private cartService: CartService,
    private countCartService: CountCartService,
    private gaService: GaService
  ) {}

  handleClick(action: actionDto) {
    if (this.onAction) {
      this.onAction = false;
      this.action.emit(action);
      setTimeout(() => {
        this.onAction = true;
      }, 500);
    }
  }

  addToCart(transactionSupplier: TransactionSupplier) {
    const cartInputsArray: CreateCartInput[] = [];
    transactionSupplier.transactionDetails.map((item) => {
      const cartInputs: CreateCartInput = {
        productVariantWarehouseId: item.productVariantWarehouse.id,
        quantity: item.productVariantQuantity
      };
      cartInputsArray.push(cartInputs);
    });

    this.addLoading = true;
    const data: CreateCartWithMultipleProductInput = {
      createCartWithMultipleProductInput: {
        cartInputs: cartInputsArray
      }
    };
    this.cartService.createCartWithMultipleProduct(data).subscribe({
      next: () => {
        this.addLoading = false;
        this.cartSuccess = true;

        setTimeout(() => {
          this.cartSuccess = false;
        }, 2000);
        this.countCartService.getCountCart();
        this.router.navigate(['cart/list']);
      },
      error: (err) => {
        const [error] = err.graphQLErrors;
        if (
          err.message === 'Supplier Tidak Aktif' ||
          err.message === 'Area not coverage.' ||
          error.extensions.code === CartErrorCodeEnum.ITEM_UNAVAILABLE ||
          error.extensions.code === CartErrorCodeEnum.OUT_OF_STOCK ||
          error.extensions.code === CartErrorCodeEnum.OUT_OF_COVERAGE_AREA_ERROR
        ) {
          this.errorAddCart.emit(true);
        }
        this.addLoading = false;
      }
    });
  }

  handleCheckout() {
    this.gaService.addEvent('History_transaction_Paynow', 'transaction', '');
    this.router
      .navigate(['/payment/list'], {
        queryParams: { id: this.historyTransaction.id }
      })
      .then(() => {
        this.paymentData.payment = {
          total: this.historyTransaction.total,
          type: PaymentDataType.BTOK,
          voucher: this.paymentData.payment.voucher
        };
      });
  }

  checkoutPod() {
    this.gaService.addEvent('History_transaction_Paynow', 'transaction', '');
    this.router
      .navigate(['/payment/list'], {
        queryParams: {
          id: this.historyTransaction.id,
          paymentType: PaymentType.PAY_ON_DELIVERY
        }
      })
      .then(() => {
        this.paymentData.payment = {
          total: this.historyTransaction.total,
          type: PaymentDataType.BTOK,
          voucher: this.paymentData.payment.voucher
        };
      });
  }

  checkStatusIndomarco() {
    this.isCheckLoading = true;
    const dataDetail: DetailTransactionSupplierInput = {
      id: this.historyTransaction.id
    };
    this.scmTransactionService
      .checkStatusTransactionSupplier(dataDetail)
      .subscribe({
        next: ({ data }: any) => {
          this.historyTransaction.status = data.status;
          this.historyTransaction.transactionStatus =
            this.transactionHelperService.convertStatus(
              data.checkStatusTransactionSupplier.status
            );
          this.isCheckLoading = false;
        },
        error: () => {
          this.isCheckLoading = false;
        }
      });
  }

  getDetailTransactionById(transactionId: string) {
    this.gaService.addEvent('History_transaction_detail', 'transaction', '');
    const data: DetailTransactionSupplierInput = {
      id: transactionId
    };
    this.scmTransactionService.detailTransactionSupplier(data).subscribe({
      next: ({ data }) => {
        if (data.detailTransactionSupplier) {
          this.addToCart(data.detailTransactionSupplier);
        }
      },
      error: (err) => {
        return err;
      }
    });
  }
}
