import { Clipboard } from '@angular/cdk/clipboard';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import {
  trigger,
  state,
  style,
  transition,
  animate
} from '@angular/animations';
import {
  CartErrorCodeEnum,
  CartService,
  CompleteTransactionInput,
  CreateCartInput,
  CreateCartWithMultipleProductInput,
  DeliveryService,
  DetailDelivery,
  DetailDeliveryInputDto,
  DetailTransactionSupplierInput,
  detailWarehouseBySupplierInputDto,
  IGetWarehouseBySupplierInputResponse,
  PaymentType,
  ResponseSupplierDetailDto,
  SCMTransactionService,
  SupplierDetailIdDto,
  SupplierService,
  TransactionPaymentMethod,
  TransactionSupplier,
  WarehouseService
} from '@youtap-web-lib';
import { TransactionHelperService } from '../../../shared/service/transaction-helper.service';
import { WindowActionService } from '../../../shared/service/window-action.service';
import { CountCartService } from '../../../shared/service/count-cart.service';
import { ModalService } from '../../../shared/service/modal/modal.service';
import dayjs from 'dayjs';
import {
  PaymentData,
  PaymentDataType
} from '../../../shared/data/payment.data';
import { AjukanKeluhanModalComponent } from '../ajukan-keluhan-modal/ajukan-keluhan-modal.component';
import { GaService } from '../../../shared/service/ga.service';

@Component({
  selector: 'detail-transaction',
  templateUrl: './detail-transaction.component.html',
  animations: [
    trigger('toastAnimation', [
      state('void', style({ opacity: 0 })),
      state('*', style({ opacity: 1 })),
      transition(':enter, :leave', [animate('0.3s')])
    ])
  ]
})
export class DetailTransactionComponent implements OnInit {
  id = '';
  loading = true;
  transactionSupplier!: TransactionSupplier;
  detailDelivery!: DetailDelivery;
  completeLoading = false;
  completeModal = false;
  confirmCompleteModal = false;
  paymentDetail!: TransactionPaymentMethod;
  transactionStatus = {
    label: '',
    color: ''
  };
  warehouseDetail!: IGetWarehouseBySupplierInputResponse;
  supplierDetail!: ResponseSupplierDetailDto;
  loadingSupplier = false;
  confirmModal = false;
  dayNow: string | undefined;
  addLoading = false;
  cartSuccess = false;
  isLoadingCheckCart = false;
  errorAddCart = false;
  constructor(
    private scmTransactionService: SCMTransactionService,
    private deliveryService: DeliveryService,
    private route: ActivatedRoute,
    private router: Router,
    private clipboard: Clipboard,
    private transactionHelperService: TransactionHelperService,
    private warehouseService: WarehouseService,
    private supplierService: SupplierService,
    private paymentData: PaymentData,
    private windowActionService: WindowActionService,
    private modalService: ModalService,
    private cartService: CartService,
    private countCartService: CountCartService,
    private gaService: GaService
  ) {}

  ngOnInit(): void {
    this.id = this.route.snapshot?.queryParamMap.get('id') ?? '';
    this.getDetailTransaction();
  }

  getDetailDelivery() {
    const data: DetailDeliveryInputDto = {
      detailDeliveryInput: {
        transactionSupplierId: this.id
      }
    };
    this.deliveryService.detailDelivery(data).subscribe({
      next: ({ data }) => {
        this.loading = false;
        this.detailDelivery = data.detailDelivery;
        if (this.transactionSupplier) {
          this.paymentDetail = {
            issuerName: this.transactionSupplier.payment?.issuerName,
            paymentType: this.transactionSupplier.payment?.paymentType,
            qty: this.transactionSupplier.totalQuantity,
            totalPrice: this.transactionSupplier.totalPriceBeforeAdjustment,
            subtotal: this.transactionSupplier.totalPriceBeforeAdjustment,
            adjustment:
              this.transactionSupplier.totalPriceBeforeDeliveryFee -
              this.transactionSupplier.totalPriceBeforeAdjustment,
            deliveryFee: data.detailDelivery.delivery.deliveryFee,
            totalPayment: this.transactionSupplier.totalPrice,
            transactionSupplierPriceAdjustments:
              this.transactionSupplier.transactionSupplierPriceAdjustments
          };
        }
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  getDetailTransaction() {
    this.loading = true;
    const data: DetailTransactionSupplierInput = {
      id: this.id
    };
    this.scmTransactionService.detailTransactionSupplier(data).subscribe({
      next: ({ data }) => {
        this.transactionSupplier = data.detailTransactionSupplier;
        this.transactionStatus = this.transactionHelperService.convertStatus(
          data.detailTransactionSupplier.status
        );
        this.getDetailDelivery();
        this.getDetailWarehouse(data.detailTransactionSupplier.supplierId);
        this.getDetailSupplier(data.detailTransactionSupplier.supplierId);
      },
      error: (err) => {
        return err;
      }
    });
  }

  checkStatusIndomarco() {
    this.loading = true;
    const dataDetail: DetailTransactionSupplierInput = {
      id: this.id
    };
    this.scmTransactionService
      .checkStatusTransactionSupplier(dataDetail)
      .subscribe({
        next: ({ data }) => {
          if (data) {
            this.transactionSupplier.status =
              data.checkStatusTransactionSupplier.status;
            this.transactionStatus =
              this.transactionHelperService.convertStatus(
                data.checkStatusTransactionSupplier.status
              );
          }
          this.loading = false;
        },
        error: () => {
          this.loading = false;
        }
      });
  }

  completeTransaction() {
    this.completeLoading = true;
    const data: CompleteTransactionInput = {
      id: this.id
    };
    this.scmTransactionService.completeTransaction(data).subscribe({
      next: () => {
        this.completeLoading = false;
        this.completeModal = false;
        this.confirmCompleteModal = true;
        this.confirmModal = true;
        this.getDetailTransaction();
        this.getDetailDelivery();
        setTimeout(() => {
          this.windowActionService.reload();
        }, 2000);
      },
      error: (err) => {
        return err;
      }
    });
  }

  confirmCompleteTransaction() {
    this.confirmCompleteModal = false;
  }

  getDetailWarehouse(id: string) {
    this.loadingSupplier = true;
    const data: detailWarehouseBySupplierInputDto = {
      detailWarehouseBySupplierInput: {
        supplierId: id
      }
    };

    this.warehouseService.getWarehouse(data).subscribe({
      next: ({ data }) => {
        this.loadingSupplier = true;
        this.warehouseDetail = data.getWarehouse;
        const today = dayjs().locale('id').format('dddd');
        const findDay = data.getWarehouse.operationalHours.find((day) => {
          return dayjs().day(day.day).locale('id').format('dddd') === today;
        });
        if (findDay) {
          this.dayNow = `${findDay.openTime} - ${findDay.closeTime}`;
        } else {
          this.dayNow = '';
        }
      },
      error: (err) => {
        this.loadingSupplier = false;
        return err;
      }
    });
  }

  getDetailSupplier(id: string) {
    this.loadingSupplier = true;
    const data: SupplierDetailIdDto = {
      detailSupplierInput: {
        id: id
      }
    };

    this.supplierService.detailSupplier(data).subscribe({
      next: ({ data }) => {
        this.loadingSupplier = false;
        this.supplierDetail = data;
      },
      error: (err) => {
        this.loadingSupplier = false;
        return err;
      }
    });
  }

  copyToClipboard(value: string) {
    this.clipboard.copy(value);
    alert('Berhasil disalin!');
  }

  handleCheckout() {
    this.gaService.addEvent('History_transaction_Paynow', 'transaction', '');
    this.router
      .navigate(['/payment/list'], {
        queryParams: { id: this.transactionSupplier.id }
      })
      .then(() => {
        if (this.transactionSupplier.invoice) {
          this.paymentData.payment = {
            total: this.paymentDetail.totalPayment,
            type: PaymentDataType.BTOK,
            voucher: this.paymentData.payment.voucher
          };
        }
      });
  }

  openComplain() {
    this.modalService.open(AjukanKeluhanModalComponent, {
      title: 'Ajukan Keluhan Ke Supplier',
      closeButton: true,
      data: {
        detail: this.transactionSupplier.supplier.slug
      }
    });
  }

  successComplain() {
    this.completeModal = !this.completeModal;
    this.openComplain();
  }

  goToHome() {
    this.router.navigate(['/']);
  }

  checkoutPod() {
    this.gaService.addEvent('History_transaction_Paynow', 'transaction', '');
    this.router
      .navigate(['/payment/list'], {
        queryParams: {
          id: this.transactionSupplier.id,
          paymentType: PaymentType.PAY_ON_DELIVERY
        }
      })
      .then(() => {
        this.paymentData.payment = {
          total: this.transactionSupplier.totalPrice,
          type: PaymentDataType.BTOK,
          voucher: this.paymentData.payment.voucher
        };
      });
  }

  addToCart() {
    this.gaService.addEvent('History_transaction_Rebuy', 'transaction', '');
    const cartInputsArray: CreateCartInput[] = [];
    this.transactionSupplier.transactionDetails.map((item) => {
      const cartInputs: CreateCartInput = {
        productVariantWarehouseId: item.productVariantWarehouse.id,
        quantity: item.productVariantQuantity
      };
      cartInputsArray.push(cartInputs);
    });

    this.addLoading = true;
    const data: CreateCartWithMultipleProductInput = {
      createCartWithMultipleProductInput: {
        cartInputs: cartInputsArray
      }
    };
    this.cartService.createCartWithMultipleProduct(data).subscribe({
      next: () => {
        this.addLoading = false;
        this.cartSuccess = true;

        setTimeout(() => {
          this.cartSuccess = false;
        }, 2000);
        this.countCartService.getCountCart();
        this.router.navigate(['cart/list']);
      },
      error: (err) => {
        const [error] = err.graphQLErrors;
        if (
          err.message === 'Supplier Tidak Aktif' ||
          error.extensions.code === CartErrorCodeEnum.ITEM_UNAVAILABLE ||
          error.extensions.code === CartErrorCodeEnum.OUT_OF_STOCK
        ) {
          this.errorAddCart = true;
          setTimeout(() => {
            this.errorAddCart = false;
          }, 5000);
        }
        this.addLoading = false;
      }
    });
  }
}
