import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomeContainerComponent } from '../../shared/layout/container/home-container/home-container.component';
import { ListTransactionComponent } from './list-transaction/list-transaction.component';
import { DetailTransactionComponent } from './detail-transaction/detail-transaction.component';
import { BasicContainerComponent } from '../../shared/layout/container/basic-container/basic-container.component';

const routes: Routes = [
  {
    path: '',
    component: HomeContainerComponent,
    children: [
      {
        path: '',
        component: ListTransactionComponent,
        title: 'Transaksi-Belanja-STOK'
      },
    ],
  },
  {
    path: 'detail',
    component: BasicContainerComponent,
    children: [
      {
        path: '',
        component: DetailTransactionComponent,
        title: 'Transaksi-Belanja-STOCK/Detail',
        data: {
          pageTitle: "Detail Transaksi",
        }
      }
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TransactionRoutingModule { }
