import { Component, Inject, OnInit, PLATFORM_ID } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location, isPlatformBrowser } from '@angular/common';
import {
  trigger,
  state,
  style,
  transition,
  animate
} from '@angular/animations';
import {
  ListTransactionSupplier,
  ListTransactionSupplierInputDto,
  OrderByTypes,
  SCMTransactionService,
  SCMTransactionSupplierStatusTypes,
  SortByTypes,
  TransactionSupplier
} from '@youtap-web-lib';
import { ModalService } from '../../../shared/service/modal/modal.service';
import { TransactionHelperService } from '../../../shared/service/transaction-helper.service';
import { AjukanKeluhanModalComponent } from '../ajukan-keluhan-modal/ajukan-keluhan-modal.component';
import { HistoryTransactionItemCardDto } from '../../../shared/components/cards/history-transaction-item-card/history-transaction-item-card.component';
import { GaService } from '../../../shared/service/ga.service';

@Component({
  selector: 'list-transaction',
  templateUrl: './list-transaction.component.html',
  animations: [
    trigger('toastAnimation', [
      state('void', style({ opacity: 0 })),
      state('*', style({ opacity: 1 })),
      transition(':enter, :leave', [animate('0.3s')])
    ])
  ]
})
export class ListTransactionComponent implements OnInit {
  loading = true;
  page = 1;
  orderBy: OrderByTypes = OrderByTypes.UPDATED_AT;
  transactionSuppliers: HistoryTransactionItemCardDto[] = [];
  listTransactionSupplier!: ListTransactionSupplier;
  status = {
    value: '*',
    label: 'Semua Status',
    modal: false
  };
  listStatus = [
    {
      label: 'Menunggu Pembayaran',
      value: SCMTransactionSupplierStatusTypes.AWAITING_PAYMENT
    },
    {
      label: 'Menunggu Konfirmasi',
      value: SCMTransactionSupplierStatusTypes.PENDING
    },
    {
      label: 'Pesanan Diproses',
      value: SCMTransactionSupplierStatusTypes.PROCESSED
    },
    {
      label: 'Pesanan Dikirim',
      value: SCMTransactionSupplierStatusTypes.ON_DELIVERY
    },
    {
      label: 'Pesanan Selesai',
      value: SCMTransactionSupplierStatusTypes.SUCCESS
    },
    {
      label: 'Pembayaran Gagal',
      value: SCMTransactionSupplierStatusTypes.FAILED
    },
    {
      label: 'Pesanan Dibatalkan',
      value: SCMTransactionSupplierStatusTypes.CANCELED
    }
  ];
  filter = {
    value: SortByTypes.DESC,
    modal: false
  };
  canLoad = true;
  errorAddCart = false;

  constructor(
    @Inject(PLATFORM_ID) private platformId: object,
    private scmTransactionService: SCMTransactionService,
    private router: Router,
    private route: ActivatedRoute,
    private location: Location,
    private modalService: ModalService,
    private transactionHelperService: TransactionHelperService,
    private gaService: GaService
  ) {
    const status =
      this.route.snapshot?.queryParamMap.get('status') || undefined;
    const filter =
      this.route.snapshot?.queryParamMap.get('filter') || undefined;
    if (status && status !== '*') {
      this.status = {
        modal: false,
        value: status,
        label: this.listStatus.filter((val) => val.value == status)[0].label
      };
    }
    if (filter) {
      this.filter = {
        modal: false,
        value: filter as SortByTypes
      };
    }
  }

  ngOnInit(): void {
    if (isPlatformBrowser(this.platformId)) {
      this.getTransactionSupplier();
    }
  }

  toHome() {
    this.router.navigate(['/']);
  }

  toDetail(transaction: HistoryTransactionItemCardDto) {
    this.gaService.addEvent('History_transaction_detail', 'transaction', '');
    this.router.navigate(['/transaction/detail'], {
      queryParams: { id: transaction.id }
    });
  }

  getTransactionSupplier() {
    this.loading = true;
    const data: ListTransactionSupplierInputDto = {
      listTransactionSupplierInput: {
        page: this.page,
        limit: 10,
        order: {
          orderBy: this.orderBy,
          sortBy: this.filter.value
        },
        filter:
          this.status.value === '*'
            ? {
                startDate: '',
                endDate: ''
              }
            : {
                startDate: '',
                endDate: '',
                status: this.status.value
              }
      }
    };
    this.scmTransactionService.listTransactionSupplier(data).subscribe({
      next: ({ data }) => {
        this.canLoad = true;
        this.loading = false;
        this.listTransactionSupplier = data.listTransactionSupplier;
        data.listTransactionSupplier.transactionSuppliers.forEach(
          (transactionSupplier) => {
            this.transactionSuppliers.push(
              this.historyTransaction(transactionSupplier)
            );
          }
        );
        this.location.replaceState(
          `/transaction?status=${this.status.value}&filter=${this.filter.value}`
        );
        if (data.listTransactionSupplier.transactionSuppliers.length === 0) {
          this.canLoad = false;
        }
      },
      error: (err) => {
        err;
      }
    });
  }

  historyTransaction(transactionSupplier: TransactionSupplier) {
    const historyTransaction: HistoryTransactionItemCardDto = {
      id: transactionSupplier.id,
      supplierImage: transactionSupplier.supplierImage,
      supplierName: transactionSupplier.supplierName,
      date: transactionSupplier.updatedAt,
      transactionStatus: this.transactionHelperService.convertStatus(
        transactionSupplier.status
      ),
      productVariantImage:
        transactionSupplier.transactionDetails[0].productVariantImage,
      productName: transactionSupplier.transactionDetails[0].productName,
      productVariantName:
        transactionSupplier.transactionDetails[0].productVariantName,
      productQty:
        transactionSupplier.transactionDetails[0].productVariantQuantity,
      productOther: transactionSupplier.transactionDetails.length,
      total: transactionSupplier.totalPrice,
      supplier: transactionSupplier.supplier,
      warehouse: transactionSupplier.warehouse,
      status: transactionSupplier.status,
      type: transactionSupplier.transaction.type,
      supplierThirdPartyType: transactionSupplier.supplierThirdPartyType,
      transactionDetails: transactionSupplier.transactionDetails
    };
    return historyTransaction;
  }

  // onScrollDown() {
  //   if (this.page < this.listTransactionSupplier.totalPage) {
  //     this.getTransactionSupplier();
  //   }
  // }

  selectStatus(status: string, label: string) {
    this.page = 1;
    this.transactionSuppliers = [];
    this.status = {
      modal: false,
      value: status,
      label
    };
    this.getTransactionSupplier();
  }

  selectFilter(sort: string) {
    this.page = 1;
    this.transactionSuppliers = [];
    this.filter = {
      modal: false,
      value: sort as SortByTypes
    };
    this.getTransactionSupplier();
  }

  action(action: string, complain: HistoryTransactionItemCardDto) {
    switch (action) {
      case 'complain':
        this.openComplain(complain);
        break;
      case 'detail':
        this.toDetail(complain);
        break;
    }
  }

  handleErrorAddCart(event: boolean) {
    this.errorAddCart = event;
    setTimeout(() => {
      this.errorAddCart = false;
    }, 5000);
  }

  openComplain(complain: HistoryTransactionItemCardDto) {
    this.modalService.open(AjukanKeluhanModalComponent, {
      title: 'Ajukan Keluhan Ke Supplier',
      closeButton: true,
      data: {
        detail: complain.supplier.slug
      }
    });
  }

  onScroll() {
    this.page += 1;
    if (this.canLoad && this.page <= this.listTransactionSupplier.totalPage) {
      this.getTransactionSupplier();
    }
  }
}
